.contacts {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding-right: 30px;
  box-sizing: border-box;
  &__title,
  &__subtitle {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
  }

  &__title {
    font-size: 24px;
    color: var(--primary-text-color, #0f0f0f);
  }

  &__subtitle {
    color: var(--text-grey-color, #9c9c9c);
    font-size: 20px;
  }
}
